.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2);

    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

    overflow-y: scroll;
}

.backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: visible;
}

.backdrop.is-hidden .modal {
    transform: translateX(-50%) scale(1.1);

    @media screen and (min-width: $desktop) {
        transform: translate(-50%, -50%) scale(1.1);
    }
}

.modal {
    position: absolute;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    max-width: 414px;
    width: 100%;

    padding: 60px 15px;
    
    background-color: $primary-bg-color;
}

.modal__close {
    position: absolute;
    top: 16px;
    right: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    border: 0;
    padding: 0;

    
}

.modal__icon-close {
    fill: black;
}

.modal-title {
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.16;
    letter-spacing: 0.05em;
    color: $primary-modal-text-color;

    text-align: center;

    margin-bottom: 40px;
}

.form-room__input {
    display: block;
    max-width: 384px;
    width: 100%;
    height: 54px;

    padding-left: 12px;
    margin-bottom: 8px;

    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.05em;

    background-color: $primary-bg-color; 
    border: 1px solid #C79002;
    
    color: $modal-placeholder-color;

    &:not(:first-child){
        margin-top: 17px;
    }
}

.form-room__label {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.02em;
    color: $brown-color;

}

.form__field-button {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    color: $secondary-modal-text-color;
    background-color: $primary-modal-text-color;
    border: 1px solid $accent-color;
    border-radius: 2px;
    width: 100%;

    padding: 20px 43px;
    margin-top: 40px;
}