  padding-left: 0;
}

select {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

button {
  outline: none;
}

.visual-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: 1px;
  padding: 0;
  list-style: none;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  height: 100%;
  width: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: visible;
}

.backdrop.is-hidden .modal {
  transform: translateX(-50%)scale(1.1);
}

@media screen and (min-width: 1280px) {
  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(1.1);
  }
}

.modal {
  max-width: 414px;
  width: 100%;
  background-color: #fffdee;
  margin: 0 auto;
  padding: 60px 15px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal__close {
  background-color: #fff;
  border: 0;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__icon-close {
  fill: #000;
}

.modal-title {
  letter-spacing: .05em;
  color: #000;
  text-align: center;
  margin-bottom: 40px;
  font-family: Times New Roman;
  font-size: 24px;
  font-style: normal;
